<template>
	<div class="addEdit-block" :style='{"minHeight":"100vh","padding":"15px ","background":"#eeeeee"}'>
		<el-form
			:style='{"padding":"30px","boxShadow":"none","borderRadius":"0","flexWrap":"wrap","background":"#fff","display":"flex"}'
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="80px"
		>
			<template >
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'" label="景点编号" prop="jingdianbianhao">
					<el-input v-model="ruleForm.jingdianbianhao" placeholder="景点编号" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-else-if="ruleForm.jingdianbianhao" label="景点编号" prop="jingdianbianhao">
					<el-input v-model="ruleForm.jingdianbianhao" placeholder="景点编号" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="景点名称" prop="jingdianmingcheng">
					<el-input v-model="ruleForm.jingdianmingcheng" placeholder="景点名称" clearable  :readonly="ro.jingdianmingcheng"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="景点名称" prop="jingdianmingcheng">
					<el-input v-model="ruleForm.jingdianmingcheng" placeholder="景点名称" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="select" v-if="type!='info'"  label="景点类型" prop="jingdianleixing">
					<el-select :disabled="ro.jingdianleixing" v-model="ruleForm.jingdianleixing" placeholder="请选择景点类型" >
						<el-option
							v-for="(item,index) in jingdianleixingOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="景点类型" prop="jingdianleixing">
					<el-input v-model="ruleForm.jingdianleixing"
						placeholder="景点类型" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="upload" v-if="type!='info' && !ro.jingdianzhaopian" label="景点照片" prop="jingdianzhaopian">
					<file-upload
						tip="点击上传景点照片"
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.jingdianzhaopian?ruleForm.jingdianzhaopian:''"
						@change="jingdianzhaopianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="upload" v-else-if="ruleForm.jingdianzhaopian" label="景点照片" prop="jingdianzhaopian">
					<img v-if="ruleForm.jingdianzhaopian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.jingdianzhaopian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.jingdianzhaopian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="select" v-if="type!='info'"  label="景点等级" prop="jingdiandengji">
					<el-select :disabled="ro.jingdiandengji" v-model="ruleForm.jingdiandengji" placeholder="请选择景点等级" >
						<el-option
							v-for="(item,index) in jingdiandengjiOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="景点等级" prop="jingdiandengji">
					<el-input v-model="ruleForm.jingdiandengji"
						placeholder="景点等级" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="门票价格" prop="menpiaojiage">
					<el-input-number v-model="ruleForm.menpiaojiage" placeholder="门票价格" :disabled="ro.menpiaojiage"></el-input-number>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="门票价格" prop="menpiaojiage">
					<el-input v-model="ruleForm.menpiaojiage" placeholder="门票价格" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="select" v-if="type!='info'"  label="景点省份" prop="shengfen">
					<el-select :disabled="ro.shengfen" v-model="ruleForm.shengfen" placeholder="请选择景点省份" >
						<el-option
							v-for="(item,index) in shengfenOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="景点省份" prop="shengfen">
					<el-input v-model="ruleForm.shengfen"
						placeholder="景点省份" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="景点地址" prop="jingdiandizhi">
					<el-input v-model="ruleForm.jingdiandizhi" placeholder="景点地址" clearable  :readonly="ro.jingdiandizhi"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="景点地址" prop="jingdiandizhi">
					<el-input v-model="ruleForm.jingdiandizhi" placeholder="景点地址" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="开放时间" prop="kaifangshijian">
					<el-input v-model="ruleForm.kaifangshijian" placeholder="开放时间" clearable  :readonly="ro.kaifangshijian"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="开放时间" prop="kaifangshijian">
					<el-input v-model="ruleForm.kaifangshijian" placeholder="开放时间" readonly></el-input>
				</el-form-item>
			</template>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="textarea" v-if="type!='info'" label="购票须知" prop="goupiaoxuzhi">
					<el-input
					  style="min-width: 200px; max-width: 600px;"
					  type="textarea"
					  :rows="8"
					  placeholder="购票须知"
					  v-model="ruleForm.goupiaoxuzhi" >
					</el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else-if="ruleForm.goupiaoxuzhi" label="购票须知" prop="goupiaoxuzhi">
					<span :style='{"fontSize":"14px","lineHeight":"40px","color":"#333","fontWeight":"500","display":"inline-block"}'>{{ruleForm.goupiaoxuzhi}}</span>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-if="type!='info'"  label="景点详情" prop="jingdianxiangqing">
					<editor 
						style="min-width: 200px; max-width: 600px;"
						v-model="ruleForm.jingdianxiangqing" 
						class="editor" 
						action="file/upload">
					</editor>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else-if="ruleForm.jingdianxiangqing" label="景点详情" prop="jingdianxiangqing">
                    <span :style='{"fontSize":"14px","lineHeight":"40px","color":"#333","fontWeight":"500","display":"inline-block"}' v-html="ruleForm.jingdianxiangqing"></span>
                </el-form-item>
			<el-form-item :style='{"width":"100%","padding":"0","margin":"0"}' class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
					提交
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
					取消
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
					返回
				</el-button>
			</el-form-item>
		</el-form>
    

  </div>
</template>
<script>
import { 
	isNumber,
	isIntNumer,
} from "@/utils/validate";
export default {
	data() {
		var validateNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isNumber(value)) {
				callback(new Error("请输入数字"));
			} else {
				callback();
			}
		};
		var validateIntNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isIntNumer(value)) {
				callback(new Error("请输入整数"));
			} else {
				callback();
			}
		};
		return {
			id: '',
			type: '',
			
			
			ro:{
				jingdianbianhao : false,
				jingdianmingcheng : false,
				jingdianleixing : false,
				jingdianzhaopian : false,
				jingdiandengji : false,
				menpiaojiage : false,
				shengfen : false,
				jingdiandizhi : false,
				kaifangshijian : false,
				goupiaoxuzhi : false,
				jingdianxiangqing : false,
				clicktime : false,
				clicknum : false,
				discussnum : false,
				storeupnum : false,
			},
			
			
			ruleForm: {
				jingdianbianhao: this.getUUID(),
				jingdianmingcheng: '',
				jingdianleixing: '',
				jingdianzhaopian: '',
				jingdiandengji: '',
				menpiaojiage: '',
				shengfen: '',
				jingdiandizhi: '',
				kaifangshijian: '',
				goupiaoxuzhi: '',
				jingdianxiangqing: '',
				clicktime: '',
			},
		
			jingdianleixingOptions: [],
			jingdiandengjiOptions: [],
			shengfenOptions: [],

			
			rules: {
				jingdianbianhao: [
				],
				jingdianmingcheng: [
				],
				jingdianleixing: [
				],
				jingdianzhaopian: [
				],
				jingdiandengji: [
				],
				menpiaojiage: [
					{ validator: validateNumber, trigger: 'blur' },
				],
				shengfen: [
				],
				jingdiandizhi: [
				],
				kaifangshijian: [
				],
				goupiaoxuzhi: [
				],
				jingdianxiangqing: [
				],
				clicktime: [
				],
				clicknum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				discussnum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				storeupnum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
			}
		};
	},
	props: ["parent"],
	computed: {



	},
    components: {
    },
	created() {
	},
	methods: {
		
		// 下载
		download(file){
			window.open(`${file}`)
		},
		// 初始化
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='jingdianbianhao'){
							this.ruleForm.jingdianbianhao = obj[o];
							this.ro.jingdianbianhao = true;
							continue;
						}
						if(o=='jingdianmingcheng'){
							this.ruleForm.jingdianmingcheng = obj[o];
							this.ro.jingdianmingcheng = true;
							continue;
						}
						if(o=='jingdianleixing'){
							this.ruleForm.jingdianleixing = obj[o];
							this.ro.jingdianleixing = true;
							continue;
						}
						if(o=='jingdianzhaopian'){
							this.ruleForm.jingdianzhaopian = obj[o];
							this.ro.jingdianzhaopian = true;
							continue;
						}
						if(o=='jingdiandengji'){
							this.ruleForm.jingdiandengji = obj[o];
							this.ro.jingdiandengji = true;
							continue;
						}
						if(o=='menpiaojiage'){
							this.ruleForm.menpiaojiage = obj[o];
							this.ro.menpiaojiage = true;
							continue;
						}
						if(o=='shengfen'){
							this.ruleForm.shengfen = obj[o];
							this.ro.shengfen = true;
							continue;
						}
						if(o=='jingdiandizhi'){
							this.ruleForm.jingdiandizhi = obj[o];
							this.ro.jingdiandizhi = true;
							continue;
						}
						if(o=='kaifangshijian'){
							this.ruleForm.kaifangshijian = obj[o];
							this.ro.kaifangshijian = true;
							continue;
						}
						if(o=='goupiaoxuzhi'){
							this.ruleForm.goupiaoxuzhi = obj[o];
							this.ro.goupiaoxuzhi = true;
							continue;
						}
						if(o=='jingdianxiangqing'){
							this.ruleForm.jingdianxiangqing = obj[o];
							this.ro.jingdianxiangqing = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
				}
			}
			// 获取用户信息
			this.$http({
				url: `${this.$storage.get('sessionTable')}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					var json = data.data;
				} else {
					this.$message.error(data.msg);
				}
			});
            this.$http({
				url: `option/jingdianleixing/jingdianleixing`,
				method: "get"
            }).then(({ data }) => {
				if (data && data.code === 0) {
					this.jingdianleixingOptions = data.data;
				} else {
					this.$message.error(data.msg);
				}
            });
            this.jingdiandengjiOptions = "★,★★,★★★,★★★★,★★★★★".split(',')
            this.$http({
				url: `option/shengfen/shengfen`,
				method: "get"
            }).then(({ data }) => {
				if (data && data.code === 0) {
					this.shengfenOptions = data.data;
				} else {
					this.$message.error(data.msg);
				}
            });
			
		},
    // 多级联动参数

    info(id) {
      this.$http({
        url: `jingdianxinxi/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
        this.ruleForm = data.data;
        //解决前台上传图片后台不显示的问题
        let reg=new RegExp('../../../upload','g')//g代表全部
        this.ruleForm.jingdianxiangqing = this.ruleForm.jingdianxiangqing.replace(reg,'../../../tourismvue5/upload');
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 提交
    onSubmit() {
		if(this.ruleForm.jingdianbianhao) {
			this.ruleForm.jingdianbianhao = String(this.ruleForm.jingdianbianhao)
		}
	if(this.ruleForm.jingdianzhaopian!=null) {
		this.ruleForm.jingdianzhaopian = this.ruleForm.jingdianzhaopian.replace(new RegExp(this.$base.url,"g"),"");
	}
var objcross = this.$storage.getObj('crossObj');
      //更新跨表属性
       var crossuserid;
       var crossrefid;
       var crossoptnum;
       if(this.type=='cross'){
                var statusColumnName = this.$storage.get('statusColumnName');
                var statusColumnValue = this.$storage.get('statusColumnValue');
                if(statusColumnName!='') {
                        var obj = this.$storage.getObj('crossObj');
                       if(statusColumnName && !statusColumnName.startsWith("[")) {
                               for (var o in obj){
                                 if(o==statusColumnName){
                                   obj[o] = statusColumnValue;
                                 }
                               }
                               var table = this.$storage.get('crossTable');
                             this.$http({
                                 url: `${table}/update`,
                                 method: "post",
                                 data: obj
                               }).then(({ data }) => {});
                       } else {
                               crossuserid=this.$storage.get('userid');
                               crossrefid=obj['id'];
                               crossoptnum=this.$storage.get('statusColumnName');
                               crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                        }
                }
        }
		this.$refs["ruleForm"].validate(valid => {
			if (valid) {
				if(crossrefid && crossuserid) {
					this.ruleForm.crossuserid = crossuserid;
					this.ruleForm.crossrefid = crossrefid;
					let params = { 
						page: 1, 
						limit: 10, 
						crossuserid:this.ruleForm.crossuserid,
						crossrefid:this.ruleForm.crossrefid,
					} 
				this.$http({ 
					url: "jingdianxinxi/page", 
					method: "get", 
					params: params 
				}).then(({ 
					data 
				}) => { 
					if (data && data.code === 0) { 
						if(data.data.total>=crossoptnum) {
							this.$message.error(this.$storage.get('tips'));
							return false;
						} else {
							this.$http({
								url: `jingdianxinxi/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "操作成功",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.jingdianxinxiCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});

						}
					} else { 
				} 
			});
		} else {
			this.$http({
				url: `jingdianxinxi/${!this.ruleForm.id ? "save" : "update"}`,
				method: "post",
			   data: this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "操作成功",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.parent.showFlag = true;
							this.parent.addOrUpdateFlag = false;
							this.parent.jingdianxinxiCrossAddOrUpdateFlag = false;
							this.parent.search();
							this.parent.contentStyleChange();
						}
					});
				} else {
					this.$message.error(data.msg);
			   }
			});
		 }
         }
       });
    },
    // 获取uuid
    getUUID () {
      return new Date().getTime();
    },
    // 返回
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.jingdianxinxiCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
    jingdianzhaopianUploadChange(fileUrls) {
	    this.ruleForm.jingdianzhaopian = fileUrls;
    },
  }
};
</script>
<style lang="scss" scoped>
	.amap-wrapper {
		width: 100%;
		height: 500px;
	}
	
	.search-box {
		position: absolute;
	}
	
	.el-date-editor.el-input {
		width: auto;
	}
	
	.add-update-preview .el-form-item /deep/ .el-form-item__label {
	  	  padding: 0 10px 0 0;
	  	  color: #9E9E9E;
	  	  font-weight: 500;
	  	  width: 80px;
	  	  font-size: 14px;
	  	  line-height: 40px;
	  	  text-align: right;
	  	}
	
	.add-update-preview .el-form-item /deep/ .el-form-item__content {
	  margin-left: 80px;
	}
	
	.add-update-preview .el-input /deep/ .el-input__inner {
	  	  border: 1px solid #C8C8C8;
	  	  border-radius: 4px;
	  	  padding: 0 12px;
	  	  box-shadow: none;
	  	  outline: none;
	  	  color: #000000;
	  	  width: 400px;
	  	  font-size: 14px;
	  	  height: 40px;
	  	}
	.add-update-preview .el-input-number /deep/ .el-input__inner {
