<template>
	<div class="addEdit-block" :style='{"minHeight":"100vh","padding":"15px ","background":"#eeeeee"}'>
		<el-form
			:style='{"padding":"30px","boxShadow":"none","borderRadius":"0","flexWrap":"wrap","background":"#fff","display":"flex"}'
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="80px"
		>
			<template >
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="酒店名称" prop="jiudianmingcheng">
					<el-input v-model="ruleForm.jiudianmingcheng" placeholder="酒店名称" clearable  :readonly="ro.jiudianmingcheng"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="酒店名称" prop="jiudianmingcheng">
					<el-input v-model="ruleForm.jiudianmingcheng" placeholder="酒店名称" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="upload" v-if="type!='info' && !ro.jiudiantupian" label="酒店图片" prop="jiudiantupian">
					<file-upload
						tip="点击上传酒店图片"
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.jiudiantupian?ruleForm.jiudiantupian:''"
						@change="jiudiantupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="upload" v-else-if="ruleForm.jiudiantupian" label="酒店图片" prop="jiudiantupian">
					<img v-if="ruleForm.jiudiantupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.jiudiantupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.jiudiantupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="select" v-if="type!='info'"  label="类别" prop="leibie">
					<el-select :disabled="ro.leibie" v-model="ruleForm.leibie" placeholder="请选择类别" >
						<el-option
							v-for="(item,index) in leibieOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="类别" prop="leibie">
					<el-input v-model="ruleForm.leibie"
						placeholder="类别" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="select" v-if="type!='info'"  label="星级" prop="xingji">
					<el-select :disabled="ro.xingji" v-model="ruleForm.xingji" placeholder="请选择星级" >
						<el-option
							v-for="(item,index) in xingjiOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="星级" prop="xingji">
					<el-input v-model="ruleForm.xingji"
						placeholder="星级" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="酒店地址" prop="jiudiandizhi">
					<el-input v-model="ruleForm.jiudiandizhi" placeholder="酒店地址" clearable  :readonly="ro.jiudiandizhi"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="酒店地址" prop="jiudiandizhi">
					<el-input v-model="ruleForm.jiudiandizhi" placeholder="酒店地址" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="select" v-if="type!='info'"  label="房间类型" prop="fangjianleixing">
					<el-select :disabled="ro.fangjianleixing" v-model="ruleForm.fangjianleixing" placeholder="请选择房间类型" >
						<el-option
							v-for="(item,index) in fangjianleixingOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="房间类型" prop="fangjianleixing">
					<el-input v-model="ruleForm.fangjianleixing"
						placeholder="房间类型" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="房间价格" prop="fangjianjiage">
					<el-input-number v-model="ruleForm.fangjianjiage" placeholder="房间价格" :disabled="ro.fangjianjiage"></el-input-number>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="房间价格" prop="fangjianjiage">
					<el-input v-model="ruleForm.fangjianjiage" placeholder="房间价格" readonly></el-input>
				</el-form-item>
			</template>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-if="type!='info'"  label="酒店介绍" prop="jiudianjieshao">
					<editor 
						style="min-width: 200px; max-width: 600px;"
						v-model="ruleForm.jiudianjieshao" 
						class="editor" 
						action="file/upload">
					</editor>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else-if="ruleForm.jiudianjieshao" label="酒店介绍" prop="jiudianjieshao">
                    <span :style='{"fontSize":"14px","lineHeight":"40px","color":"#333","fontWeight":"500","display":"inline-block"}' v-html="ruleForm.jiudianjieshao"></span>
                </el-form-item>
			<el-form-item :style='{"width":"100%","padding":"0","margin":"0"}' class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
					提交
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
					取消
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
					返回
				</el-button>
			</el-form-item>
		</el-form>
    

  </div>
</template>
<script>
import { 
	isNumber,
	isIntNumer,
} from "@/utils/validate";
export default {
	data() {
		var validateNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isNumber(value)) {
				callback(new Error("请输入数字"));
			} else {
				callback();
			}
		};
		var validateIntNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isIntNumer(value)) {
				callback(new Error("请输入整数"));
			} else {
				callback();
			}
		};
		return {
			id: '',
			type: '',
			
			
			ro:{
				jiudianmingcheng : false,
				jiudiantupian : false,
				leibie : false,
				xingji : false,
				jiudiandizhi : false,
				fangjianleixing : false,
				fangjianjiage : false,
				jiudianjieshao : false,
				discussnum : false,
				storeupnum : false,
			},
			
			
			ruleForm: {
				jiudianmingcheng: '',
				jiudiantupian: '',
				leibie: '',
				xingji: '',
				jiudiandizhi: '',
				fangjianleixing: '',
				fangjianjiage: '',
				jiudianjieshao: '',
			},
		
			leibieOptions: [],
			xingjiOptions: [],
			fangjianleixingOptions: [],

			
			rules: {
				jiudianmingcheng: [
					{ required: true, message: '酒店名称不能为空', trigger: 'blur' },
				],
				jiudiantupian: [
				],
				leibie: [
					{ required: true, message: '类别不能为空', trigger: 'blur' },
				],
				xingji: [
					{ required: true, message: '星级不能为空', trigger: 'blur' },
				],
				jiudiandizhi: [
				],
				fangjianleixing: [
				],
				fangjianjiage: [
					{ validator: validateNumber, trigger: 'blur' },
				],
				jiudianjieshao: [
				],
				discussnum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				storeupnum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
			}
		};
	},
	props: ["parent"],
	computed: {



	},
    components: {
    },
	created() {
	},
	methods: {
		
		// 下载
		download(file){
			window.open(`${file}`)
		},
		// 初始化
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='jiudianmingcheng'){
							this.ruleForm.jiudianmingcheng = obj[o];
							this.ro.jiudianmingcheng = true;
							continue;
						}
						if(o=='jiudiantupian'){
							this.ruleForm.jiudiantupian = obj[o];
							this.ro.jiudiantupian = true;
							continue;
						}
						if(o=='leibie'){
							this.ruleForm.leibie = obj[o];
							this.ro.leibie = true;
							continue;
						}
						if(o=='xingji'){
							this.ruleForm.xingji = obj[o];
							this.ro.xingji = true;
							continue;
						}
						if(o=='jiudiandizhi'){
							this.ruleForm.jiudiandizhi = obj[o];
							this.ro.jiudiandizhi = true;
							continue;
						}
						if(o=='fangjianleixing'){
							this.ruleForm.fangjianleixing = obj[o];
							this.ro.fangjianleixing = true;
							continue;
						}
						if(o=='fangjianjiage'){
							this.ruleForm.fangjianjiage = obj[o];
							this.ro.fangjianjiage = true;
							continue;
						}
						if(o=='jiudianjieshao'){
							this.ruleForm.jiudianjieshao = obj[o];
							this.ro.jiudianjieshao = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
				}
			}
			// 获取用户信息
			this.$http({
				url: `${this.$storage.get('sessionTable')}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					var json = data.data;
				} else {
					this.$message.error(data.msg);
				}
			});
            this.leibieOptions = "民宿,连锁酒店,快捷酒店,豪华酒店".split(',')
            this.xingjiOptions = "一星,二星,三星,四星,五星".split(',')
            this.fangjianleixingOptions = "单人间,大床房,双人间,三人间,标准套间,豪华套间".split(',')
			
		},
    // 多级联动参数

    info(id) {
      this.$http({
        url: `jiudianxinxi/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
        this.ruleForm = data.data;
        //解决前台上传图片后台不显示的问题
        let reg=new RegExp('../../../upload','g')//g代表全部
        this.ruleForm.jiudianjieshao = this.ruleForm.jiudianjieshao.replace(reg,'../../../tourismvue5/upload');
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 提交
    onSubmit() {
	if(this.ruleForm.jiudiantupian!=null) {
		this.ruleForm.jiudiantupian = this.ruleForm.jiudiantupian.replace(new RegExp(this.$base.url,"g"),"");
	}
var objcross = this.$storage.getObj('crossObj');
      //更新跨表属性
       var crossuserid;
       var crossrefid;
       var crossoptnum;
       if(this.type=='cross'){
                var statusColumnName = this.$storage.get('statusColumnName');
                var statusColumnValue = this.$storage.get('statusColumnValue');
                if(statusColumnName!='') {
                        var obj = this.$storage.getObj('crossObj');
                       if(statusColumnName && !statusColumnName.startsWith("[")) {
                               for (var o in obj){
                                 if(o==statusColumnName){
                                   obj[o] = statusColumnValue;
                                 }
                               }
                               var table = this.$storage.get('crossTable');
                             this.$http({
                                 url: `${table}/update`,
                                 method: "post",
                                 data: obj
                               }).then(({ data }) => {});
                       } else {
                               crossuserid=this.$storage.get('userid');
                               crossrefid=obj['id'];
                               crossoptnum=this.$storage.get('statusColumnName');
                               crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                        }
                }
        }
		this.$refs["ruleForm"].validate(valid => {
			if (valid) {
				if(crossrefid && crossuserid) {
					this.ruleForm.crossuserid = crossuserid;
					this.ruleForm.crossrefid = crossrefid;
					let params = { 
						page: 1, 
						limit: 10, 
						crossuserid:this.ruleForm.crossuserid,
						crossrefid:this.ruleForm.crossrefid,
					} 
				this.$http({ 
					url: "jiudianxinxi/page", 
					method: "get", 
					params: params 
				}).then(({ 
					data 
				}) => { 
					if (data && data.code === 0) { 
						if(data.data.total>=crossoptnum) {
							this.$message.error(this.$storage.get('tips'));
							return false;
						} else {
							this.$http({
								url: `jiudianxinxi/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "操作成功",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.jiudianxinxiCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});

						}
					} else { 
				} 
			});
		} else {
			this.$http({
				url: `jiudianxinxi/${!this.ruleForm.id ? "save" : "update"}`,
				method: "post",
			   data: this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "操作成功",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.parent.showFlag = true;
							this.parent.addOrUpdateFlag = false;
							this.parent.jiudianxinxiCrossAddOrUpdateFlag = false;
							this.parent.search();
							this.parent.contentStyleChange();
						}
					});
				} else {
					this.$message.error(data.msg);
			   }
			});
		 }
         }
       });
    },
    // 获取uuid
    getUUID () {
      return new Date().getTime();
    },
    // 返回
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.jiudianxinxiCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
    jiudiantupianUploadChange(fileUrls) {
	    this.ruleForm.jiudiantupian = fileUrls;
    },
  }
};
</script>
