<template>
	<div class="addEdit-block" :style='{"minHeight":"100vh","padding":"15px ","background":"#eeeeee"}'>
		<el-form
			:style='{"padding":"30px","boxShadow":"none","borderRadius":"0","flexWrap":"wrap","background":"#fff","display":"flex"}'
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="80px"
		>
			<template >
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'" label="美食编号" prop="meishibianhao">
					<el-input v-model="ruleForm.meishibianhao" placeholder="美食编号" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-else-if="ruleForm.meishibianhao" label="美食编号" prop="meishibianhao">
					<el-input v-model="ruleForm.meishibianhao" placeholder="美食编号" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="美食名称" prop="meishimingcheng">
					<el-input v-model="ruleForm.meishimingcheng" placeholder="美食名称" clearable  :readonly="ro.meishimingcheng"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="美食名称" prop="meishimingcheng">
					<el-input v-model="ruleForm.meishimingcheng" placeholder="美食名称" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="select" v-if="type!='info'"  label="美食分类" prop="meishifenlei">
					<el-select :disabled="ro.meishifenlei" v-model="ruleForm.meishifenlei" placeholder="请选择美食分类" >
						<el-option
							v-for="(item,index) in meishifenleiOptions"
							v-bind:key="index"
							:label="item"
							:value="item">
						</el-option>
					</el-select>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="美食分类" prop="meishifenlei">
					<el-input v-model="ruleForm.meishifenlei"
						placeholder="美食分类" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="upload" v-if="type!='info' && !ro.meishitupian" label="美食图片" prop="meishitupian">
					<file-upload
						tip="点击上传美食图片"
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.meishitupian?ruleForm.meishitupian:''"
						@change="meishitupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="upload" v-else-if="ruleForm.meishitupian" label="美食图片" prop="meishitupian">
					<img v-if="ruleForm.meishitupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.meishitupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.meishitupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="美食主料" prop="meishizhuliao">
					<el-input v-model="ruleForm.meishizhuliao" placeholder="美食主料" clearable  :readonly="ro.meishizhuliao"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="美食主料" prop="meishizhuliao">
					<el-input v-model="ruleForm.meishizhuliao" placeholder="美食主料" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="美食口味" prop="meishikouwei">
					<el-input v-model="ruleForm.meishikouwei" placeholder="美食口味" clearable  :readonly="ro.meishikouwei"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="美食口味" prop="meishikouwei">
					<el-input v-model="ruleForm.meishikouwei" placeholder="美食口味" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="美食特色" prop="meishitese">
					<el-input v-model="ruleForm.meishitese" placeholder="美食特色" clearable  :readonly="ro.meishitese"></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="美食特色" prop="meishitese">
					<el-input v-model="ruleForm.meishitese" placeholder="美食特色" readonly></el-input>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' class="input" v-if="type!='info'"  label="美食价格" prop="meishijiage">
					<el-input-number v-model="ruleForm.meishijiage" placeholder="美食价格" :disabled="ro.meishijiage"></el-input-number>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else class="input" label="美食价格" prop="meishijiage">
					<el-input v-model="ruleForm.meishijiage" placeholder="美食价格" readonly></el-input>
				</el-form-item>
			</template>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-if="type!='info'"  label="美食详情" prop="meishixiangqing">
					<editor 
						style="min-width: 200px; max-width: 600px;"
						v-model="ruleForm.meishixiangqing" 
						class="editor" 
						action="file/upload">
					</editor>
				</el-form-item>
				<el-form-item :style='{"width":"45%","margin":"0 0 20px 0"}' v-else-if="ruleForm.meishixiangqing" label="美食详情" prop="meishixiangqing">
                    <span :style='{"fontSize":"14px","lineHeight":"40px","color":"#333","fontWeight":"500","display":"inline-block"}' v-html="ruleForm.meishixiangqing"></span>
                </el-form-item>
			<el-form-item :style='{"width":"100%","padding":"0","margin":"0"}' class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
					提交
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
					取消
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
					返回
				</el-button>
			</el-form-item>
		</el-form>
    

  </div>
</template>
<script>
import { 
	isNumber,
	isIntNumer,
} from "@/utils/validate";
export default {
	data() {
		var validateNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isNumber(value)) {
				callback(new Error("请输入数字"));
			} else {
				callback();
			}
		};
		var validateIntNumber = (rule, value, callback) => {
			if(!value){
				callback();
			} else if (!isIntNumer(value)) {
				callback(new Error("请输入整数"));
			} else {
				callback();
			}
		};
		return {
			id: '',
			type: '',
			
			
			ro:{
				meishibianhao : false,
				meishimingcheng : false,
				meishifenlei : false,
				meishitupian : false,
				meishizhuliao : false,
				meishikouwei : false,
				meishitese : false,
				meishijiage : false,
				meishixiangqing : false,
				discussnum : false,
				storeupnum : false,
			},
			
			
			ruleForm: {
				meishibianhao: this.getUUID(),
				meishimingcheng: '',
				meishifenlei: '',
				meishitupian: '',
				meishizhuliao: '',
				meishikouwei: '',
				meishitese: '',
				meishijiage: '',
				meishixiangqing: '',
			},
		
			meishifenleiOptions: [],

			
			rules: {
				meishibianhao: [
				],
				meishimingcheng: [
					{ required: true, message: '美食名称不能为空', trigger: 'blur' },
				],
				meishifenlei: [
					{ required: true, message: '美食分类不能为空', trigger: 'blur' },
				],
				meishitupian: [
				],
				meishizhuliao: [
				],
				meishikouwei: [
				],
				meishitese: [
				],
				meishijiage: [
					{ validator: validateNumber, trigger: 'blur' },
				],
				meishixiangqing: [
				],
				discussnum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
				storeupnum: [
					{ validator: validateIntNumber, trigger: 'blur' },
				],
			}
		};
	},
	props: ["parent"],
	computed: {



	},
    components: {
    },
	created() {
	},
	methods: {
		
		// 下载
		download(file){
			window.open(`${file}`)
		},
		// 初始化
		init(id,type) {
			if (id) {
				this.id = id;
				this.type = type;
			}
			if(this.type=='info'||this.type=='else'){
				this.info(id);
			}else if(this.type=='logistics'){
				this.logistics=false;
				this.info(id);
			}else if(this.type=='cross'){
				var obj = this.$storage.getObj('crossObj');
				for (var o in obj){
						if(o=='meishibianhao'){
							this.ruleForm.meishibianhao = obj[o];
							this.ro.meishibianhao = true;
							continue;
						}
						if(o=='meishimingcheng'){
							this.ruleForm.meishimingcheng = obj[o];
							this.ro.meishimingcheng = true;
							continue;
						}
						if(o=='meishifenlei'){
							this.ruleForm.meishifenlei = obj[o];
							this.ro.meishifenlei = true;
							continue;
						}
						if(o=='meishitupian'){
							this.ruleForm.meishitupian = obj[o];
							this.ro.meishitupian = true;
							continue;
						}
						if(o=='meishizhuliao'){
							this.ruleForm.meishizhuliao = obj[o];
							this.ro.meishizhuliao = true;
							continue;
						}
						if(o=='meishikouwei'){
							this.ruleForm.meishikouwei = obj[o];
							this.ro.meishikouwei = true;
							continue;
						}
						if(o=='meishitese'){
							this.ruleForm.meishitese = obj[o];
							this.ro.meishitese = true;
							continue;
						}
						if(o=='meishijiage'){
							this.ruleForm.meishijiage = obj[o];
							this.ro.meishijiage = true;
							continue;
						}
						if(o=='meishixiangqing'){
							this.ruleForm.meishixiangqing = obj[o];
							this.ro.meishixiangqing = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
				}
			}
			// 获取用户信息
			this.$http({
				url: `${this.$storage.get('sessionTable')}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					var json = data.data;
				} else {
					this.$message.error(data.msg);
				}
			});
            this.$http({
				url: `option/meishifenlei/meishifenlei`,
				method: "get"
            }).then(({ data }) => {
				if (data && data.code === 0) {
					this.meishifenleiOptions = data.data;
				} else {
					this.$message.error(data.msg);
				}
            });
			
		},
    // 多级联动参数

    info(id) {
      this.$http({
        url: `meishixinxi/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
        this.ruleForm = data.data;
        //解决前台上传图片后台不显示的问题
        let reg=new RegExp('../../../upload','g')//g代表全部
        this.ruleForm.meishixiangqing = this.ruleForm.meishixiangqing.replace(reg,'../../../tourismvue5/upload');
        } else {
          this.$message.error(data.msg);
        }
      });
    },


    // 提交
    onSubmit() {
		if(this.ruleForm.meishibianhao) {
			this.ruleForm.meishibianhao = String(this.ruleForm.meishibianhao)
		}
	if(this.ruleForm.meishitupian!=null) {
		this.ruleForm.meishitupian = this.ruleForm.meishitupian.replace(new RegExp(this.$base.url,"g"),"");
	}
var objcross = this.$storage.getObj('crossObj');
      //更新跨表属性
       var crossuserid;
       var crossrefid;
       var crossoptnum;
       if(this.type=='cross'){
                var statusColumnName = this.$storage.get('statusColumnName');
                var statusColumnValue = this.$storage.get('statusColumnValue');
                if(statusColumnName!='') {
                        var obj = this.$storage.getObj('crossObj');
                       if(statusColumnName && !statusColumnName.startsWith("[")) {
                               for (var o in obj){
                                 if(o==statusColumnName){
                                   obj[o] = statusColumnValue;
                                 }
                               }
                               var table = this.$storage.get('crossTable');
                             this.$http({
                                 url: `${table}/update`,
                                 method: "post",
                                 data: obj
                               }).then(({ data }) => {});
                       } else {
                               crossuserid=this.$storage.get('userid');
                               crossrefid=obj['id'];
                               crossoptnum=this.$storage.get('statusColumnName');
                               crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
                        }
                }
        }
		this.$refs["ruleForm"].validate(valid => {
			if (valid) {
				if(crossrefid && crossuserid) {
					this.ruleForm.crossuserid = crossuserid;
					this.ruleForm.crossrefid = crossrefid;
					let params = { 
						page: 1, 
						limit: 10, 
						crossuserid:this.ruleForm.crossuserid,
						crossrefid:this.ruleForm.crossrefid,
					} 
				this.$http({ 
					url: "meishixinxi/page", 
					method: "get", 
					params: params 
				}).then(({ 
					data 
				}) => { 
					if (data && data.code === 0) { 
						if(data.data.total>=crossoptnum) {
							this.$message.error(this.$storage.get('tips'));
							return false;
						} else {
							this.$http({
								url: `meishixinxi/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "操作成功",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.meishixinxiCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});

						}
					} else { 
				} 
			});
		} else {
			this.$http({
				url: `meishixinxi/${!this.ruleForm.id ? "save" : "update"}`,
				method: "post",
			   data: this.ruleForm
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.$message({
						message: "操作成功",
						type: "success",
						duration: 1500,
						onClose: () => {
							this.parent.showFlag = true;
							this.parent.addOrUpdateFlag = false;
							this.parent.meishixinxiCrossAddOrUpdateFlag = false;
							this.parent.search();
							this.parent.contentStyleChange();
						}
					});
				} else {
					this.$message.error(data.msg);
			   }
			});
		 }
         }
       });
    },
    // 获取uuid
    getUUID () {
      return new Date().getTime();
    },
    // 返回
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.meishixinxiCrossAddOrUpdateFlag = false;
      this.parent.contentStyleChange();
    },
    meishitupianUploadChange(fileUrls) {
	    this.ruleForm.meishitupian = fileUrls;
    },
  }
};
</script>
<style lang="scss" scoped>
