		vertical-align: middle;
	}
	
	.add-update-preview /deep/ .upload .upload-img {
	  	  border: 1px solid    #C8C8C8;
	  	  cursor: pointer;
	  	  border-radius: 0;
	  	  color: #000000;
	  	  font-weight: bold;
	  	  width: 150px;
	  	  font-size: 32px;
	  	  line-height: 150px;
	  	  text-align: center;
	  	  height: 150px;
	  	}
	
	.add-update-preview /deep/ .el-upload-list .el-upload-list__item {
	  	  border: 1px solid    #C8C8C8;
	  	  cursor: pointer;
	  	  border-radius: 0;
	  	  color: #000000;
	  	  font-weight: bold;
	  	  width: 150px;
	  	  font-size: 32px;
	  	  line-height: 150px;
	  	  text-align: center;
	  	  height: 150px;
	  	}
	
	.add-update-preview /deep/ .el-upload .el-icon-plus {
	  	  border: 1px solid    #C8C8C8;
	  	  cursor: pointer;
	  	  border-radius: 0;
	  	  color: #000000;
	  	  font-weight: bold;
	  	  width: 150px;
	  	  font-size: 32px;
	  	  line-height: 150px;
	  	  text-align: center;
	  	  height: 150px;
	  	}
	
	.add-update-preview .el-textarea /deep/ .el-textarea__inner {
	  	  border: 1px solid    #C8C8C8;
	  	  border-radius: 4px;
	  	  padding: 12px;
	  	  box-shadow: none;
	  	  outline: none;
	  	  color:  #C8C8C8;
	  	  width: 400px;
	  	  font-size: 14px;
	  	  height: 120px;
	  	}
	
	.add-update-preview .btn .btn1 {
				border: 0;
				cursor: pointer;
				border-radius: 4px;
				padding: 0 24px;
				margin: 4px;
				outline: none;
				color: #fff;
				background: #6C549C;
				width: auto;
				font-size: 14px;
